<template>
    <div class="mproduct">
        <i-nav></i-nav>
        <div class="herderSerch">
            <el-input style="width:300px"
                placeholder="请输入商品名称"
                v-model="name"
                clearable>
            </el-input>
            <el-button type="primary" @click="search()" style="width:80px;"><i class="el-icon-search"></i>搜 索</el-button>
        </div>
        <el-breadcrumb separator-class="el-icon-arrow-right" style="padding-left:450px;padding-top:40px;padding-bottom: 40px">
            <el-breadcrumb-item :to="{ path: '/' }" style="font-size: 15px;" >首页</el-breadcrumb-item>
            <el-breadcrumb-item>商品展示</el-breadcrumb-item>
        </el-breadcrumb>

        <el-row style="padding-left:450px">
            <el-col  v-for="item in mlist" :key="item.productId" style="height:150px;width:800px;padding:2px;float:none;text-align:center">
                
                <el-card 
                    shadow="hover" 
                    :body-style="{ padding: '10px',margin:' 10px', width:'700px',heght:'150px'}">
                    <img @click="JumpToDetail(item.productId)" v-bind:src="(path)+(item.productPhoto)" class="image"/>

                    <div class="infom" style="padding-top:30px;height:70px;">
                        <span style="font-size:16px">{{item.productName}}</span><br>
                        <span>{{item.productSmallPrice}}元</span>
                        <div class="bottom clearfix">
                        <time class="time">{{ }}</time>
                        <el-button @click.native="AddShopCar(item.productId)" type="text" class="button">加入购物车</el-button>
                        </div>
                    </div>
                </el-card>

            </el-col>
        </el-row>

         <el-backtop right="80" :bottom="80">
            <div
              style="{
                                height: 100%;
                                width: 100%;
                                box-shadow: 0 0 6px rgba(0,0,0, .12);
                                text-align: center;
                                line-height: 40px;
                                color: #1989fa;
                                border-radius: 20px;
                            }"
            >
              <i class="el-icon-caret-top"></i>
            </div>
          </el-backtop>

    </div>
</template>
<script>
import HeaderSerch from './HeaderSerch.vue'
import Nav from '@/components/Common/NavgationBar.vue';
export default {
    data(){
        return{
            name : '',
            mlist:'',
            path:'http://www.wxhwg.com:8080/mock/static/'
        }
    },
    created(){
        this.init();
    },
    components:{
        'i-headerSerch':HeaderSerch,
        'i-nav' : Nav
    },
    methods:{
        init(){
            this.name = this.$route.query.prolist;
            this.search();
        },
        search(){
            this.$axios({
                method:'get',
                url:'http://www.wxhwg.com:8080/mock/api/product/search',
                params:{
                    name:this.name}
            }).then(response=>{
                let result=response.data;
                if(result.status=='200'){
                    this.mlist=result.data;
                }
            })
        },
        get(){
            this.list=parseInt(this.$route.query.prolist);
            alert(this.list)
        },
        JumpToDetail(productID){
            this.$router.push({path:'/productdetail',query:{id : productID}});
        },

        // // 添加购物车
        AddShopCar(productId){
            // 判断登录状态
            let user = JSON.parse(this.$store.state.loginUser);
            if(!user){
                var mymessage=confirm("请先登录账号！");
                if(mymessage==true){   
                    this.$router.push({path:'/login'});
                }
                return;
            }
            let uuid = JSON.parse(this.$store.state.loginUser).userId;

            // 查询单个商品信息
            this.$axios
            .get("http://www.wxhwg.com:8080/mock/api/product/find", {
                params: {
                    productId : productId
                }
            })
            .then(response => {
                let result = response.data;
                if (result.status == 200) {
                    let currentProduct = result.data;
                    
                    // 加入购物车
                    this.$axios({
                        method: "post",
                        url: "http://www.wxhwg.com:8080/mock/api/detail/join",
                        data: this.$qs.stringify({
                            detailProductId: currentProduct.productId, // 传参
                            detailProductCount: 1,
                            detailProductUnit: currentProduct.productLargerUnit,
                            detailProductPrice: currentProduct.productSmallPrice,
                            detailComment: "",
                            detailUserId: uuid
                        })
                    }).then(res => {
                        let result = res.data;
                        if (result.status == 200) {
                            console.log(result.message);
                            this.$message.success("加入购物车成功！");
                        } else {
                            this.$message.warning("加入购物车失败！");
                        }
                    });

                }
            })
            .catch(error => {
                alert(error);
            });
            
        }




    }
}
</script>




<style scoped>
    .mproduct{
        background-color: #f7f7f6;
    }
    .infom span { 
        display:block;
        padding-left: 100px; 
        width: 200px; 
        float:left; 
        text-align:left;
    }
    .time {
        font-size: 13px;
        color: #999;
    }
    
    .bottom {
        margin-top: 13px;
        line-height: 12px;
    }

    .button {
        padding: 0;
        float: right;
    }

    .image {
        padding-top: 20px;
        width: 90px;
        height: 70px;
        float: left;
        display: block;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }
    
    .clearfix:after {
        clear: both
    }
</style>
